import React, { PureComponent } from "react";
import Image from "./Image";
import reactImageSize from 'react-image-size';
import CardIcons from "../../Elements/CardIcons";
import {convertObjectToArray} from '../../../../utils'
export default class Media extends PureComponent {


  constructor(props) {
    super(props);
    this.myRef = React.createRef();
    this.state = {
      height: '100',
      width: '100',
    
    };
  }

  componentWillMount() {
    const { item } = this.props; 
    if(item.mediaHeight && item.mediaWidth !=""){
      this.setState({
        height : (item.mediaHeight * 100) / item.mediaWidth,
         width : (item.mediaWidth * 100) / item.mediaHeight,
      })
      
    }
      else{
         
        reactImageSize(item.postFileNew).then(({ width, height }) => this.setState({
          height: (height * 100) / width,
          width: (width * 100) / height
        })).catch((errorMessage) =>
        this.setState({
          height: '100',
          width: '100',
          error: errorMessage,
        }))
      }
    }
    render() {  
    const {item, wall, cardWidth} = this.props;
    const {height,width} = this.state
    const isShopIcon = (item.ugc_products && item.ugc_products.length > 0 && item.hotspot === 0) ? true : false;  
    const isVideo = (item.type===3 || item.type === 5) ? true : false; 
    const multiImages = (item.imageList && item.imageList.length > 0) ? true : false


    const ugc_products = convertObjectToArray(item.ugc_products);
    return (
      
      <div className="tb_cls_media_wrap">
          {isShopIcon || isVideo || multiImages? <CardIcons cardWidth={cardWidth} item={item} wall={wall}/> :''}
          {width ?<Image  ImageClass={'ts_csl_image'} height={height} width={width} ImageUrl={item.postFileNew} ugc_products={ugc_products} item={item} wall={wall} />:''}
      </div>
    );
  }
}