import React, { PureComponent } from "react";
import reactImageSize from 'react-image-size';
import HotSpot from "../../Elements/HotSpot";
import { IMG_LOAD_AFTER_COMPLETE_LAYOUT, IS_SCREEN_SHOT, marginTop_mainDiv } from '../../../../utils'
export default class Image extends PureComponent {


  componentDidMount() {
    marginTop_mainDiv(99)
  }


  render() {
    const { ImageClass, ImageUrl, item, ugc_products, wall ,height,width } = this.props

    const styleDiv ={ height: item.hotspot ? (width > height) ?`auto` : `100%`:`auto`, width: item.hotspot ? (width > height) ? `100%`: `${width}%`:`100%`, aspectRatio: (item.hotspot && (width > height)) ? `auto` :``}

    const styleImg ={ height: item.hotspot ? `100%`:`auto`, width:`100%`}
    const ImageBg = { backgroundImage: `url(${ImageUrl})`};
   
    
    return (<>
        <div className="ts_cs_image_blur_bg" style={ImageBg}><div></div></div>
        <div className={ImageClass} style={styleDiv}>
          <div className="ts_cst_inner_img_overflow" >
        
        <img loading="lazy"   src={ImageUrl} data-src={ImageUrl} className={`imgID${item.id}`} data-is-optmized="0" data-link={item.link} data-load="0" data-wall-id={wall.Wall.id} data-item-id={item.id} onLoad={this.onLoadImage} data-filter-id={item.filterId} data-stories={item.stories} data-network={item.networkId} width={width} onError={(e) => { if(item.networkId !== `28`) IMG_LOAD_AFTER_COMPLETE_LAYOUT(e) }} height={height}  style={styleImg} alt="image"/>
        </div>
        {item.hotspot ? <HotSpot ugc_products={ugc_products} UgcSetting={item.ugc_personalizaion.UgcSetting} item={item} /> : null}
    
      </div>
      </>
    );
  }
}