import React, { PureComponent } from "react";
import Card from "./elements/Card";
import { connect } from 'react-redux';
import { getDataNextSteps , themePostTracking, showPopUP } from '../../../../../../../../../actions/themeActions/themeActions'
import { numPost, IS_SCREEN_SHOT , findFromArray, removeTransFromCss } from '../../../utils'
import '@splidejs/splide/css';
import { Splide, SplideSlide, SplideTrack } from '@splidejs/react-splide';


class CircleSlider extends PureComponent {
  constructor(props) {
    super(props)
    this.secondaryRef = React.createRef();
  }
  onUpdateData = () => {
    setTimeout(() => this.requestData(), 1000)
  }

  requestData = () => {
    const { wall, postData, appendData, hasMoreData, loaderData } = this.props;
    const postCount = wall.ThemeRule.numberOfPosts;
    const timeStamp = Math.floor(Date.now() / 1000);
    if (hasMoreData && !loaderData.isShowMoreLoading) this.props.getDataNextSteps(wall.Wall.id, timeStamp, postCount, appendData.networkID, appendData.after, appendData.heightEvent);
  }

  onLoadPopup = (itemIndex, itemData) => {
    const {  wall,   postData, languageSetting, completeDataObject, wallId, webFilters } = this.props;
    let updatePostData = postData.map(itemData => completeDataObject[itemData])
    let filteredPostData = updatePostData.filter((filterData) => !String(filterData.id).includes("free_add_")) 
    let currentIndex = findFromArray(filteredPostData, itemData.id, itemIndex)

    if (wall.Personalization.postFeatured === 1) {
      removeTransFromCss(`add`);
      this.props.showPopUP({ type: 'post', card: itemData, idArray: filteredPostData, index: currentIndex, viewOnText: languageSetting.viewOnText, shareText: languageSetting.shareText, personalization: wall.Personalization, ThemeRule: wall.ThemeRule, webFilters: webFilters, UserRule: wall.UserRule, wall: wall })
    } themePostTracking({ action: 2, wall: wallId, feed: itemData.feedId, post: itemData.referenceId })
  }

  componentDidUpdate(prevProps, prevState) {
    console.log("this.props.wall?.Personalization?.columnCount--",this.props.wall)
    if (prevProps.wall?.ThemeRule?.numberOfCoumn !== this.props.wall?.ThemeRule?.numberOfCoumn) {
      this.forceUpdate();
    }
  }


  render() {
    const { appendData, postData, completeDataObject, adjustWidth, hasMoreData, wall, webFilters, languageSetting, newPostData , loaderData } = this.props;

    const columnSize = wall.ThemeRule.numberOfCoumn;
    const mobileColumnSize = wall.ThemeRule.mobileColumn;
    const infinite = hasMoreData ? false : true
    const postCount = postData.length;

    const sliderSettings1 = {
      type: 'slider',
      perPage: columnSize == 0 ? 6 : columnSize,
      perMove: wall.ThemeRule.slidePost == 1 ? 1 : columnSize == 0 ? 6 : columnSize,
      pagination: false,
      breakpoints: {
        1400: {
          // type: postCount < (columnSize == 0 ? 5 : columnSize) ? false : 'loop',
          perPage: columnSize == 0 ? 5 : columnSize,
          perMove: wall.ThemeRule.slidePost == 1 ? 1 : columnSize == 0 ? 5 : columnSize,
        },
        1024: {
          // type: postCount < (columnSize == 0 ? 4 : columnSize) ? false : 'loop',
          perPage: columnSize == 0 ? 4 : columnSize,
          perMove: wall.ThemeRule.slidePost == 1 ? 1 : columnSize == 0 ? 4 : columnSize,
        },
        767: {
          // type: postCount < (columnSize == 0 ? 3 : columnSize) ? false : 'loop',
          perPage: mobileColumnSize == 0 ? 3 : mobileColumnSize,
          perMove: wall.ThemeRule.slidePost == 1 ? 1 : mobileColumnSize == 0 ? 3 : mobileColumnSize,
        },
        560: {
          // type: postCount < (columnSize == 0 ? 2 : columnSize) ? false : 'loop',
          perPage: mobileColumnSize == 0 ? 2 : mobileColumnSize,
          perMove: wall.ThemeRule.slidePost == 1 ? 1 : mobileColumnSize == 0 ? 2 : mobileColumnSize,
        },
        480: {
          // type: postCount < (columnSize == 0 ? 1 : columnSize) ? false : 'loop',
          perPage: mobileColumnSize == 0 ? 1 : mobileColumnSize,
          perMove: wall.ThemeRule.slidePost == 1 ? 1 : mobileColumnSize == 0 ? 1 : mobileColumnSize,
        },
      },

    }


    return (
      <div className="ts_csl_post_container" id="ts_theme_container">

        <Splide hasTrack={false}   options={sliderSettings1}  ref={this.secondaryRef}>
          <SplideTrack>
            {
              (newPostData && newPostData.length > 0) ? newPostData.map((item, index) => {
                // if (IS_SCREEN_SHOT && postData && ((postData.length - 1) == index)) {
                //   setTimeout(() => {
                //     let dataUpdateImage = document.querySelectorAll("img[data-is-optmized='0']");
                //     if (dataUpdateImage && dataUpdateImage.length > 0) {
                //       dataUpdateImage.forEach((item) => {
                //         item.setAttribute("src", item.getAttribute("data-src"))
                //         item.setAttribute("data-is-optmized", "1")
                //       })
                //     }
                //   }, 1300)
                // }
                const cardData = completeDataObject[item];
                return <SplideSlide><Card itemData={cardData} key={`post_id_${index}`} itemIndex={index} {...this.props} /></SplideSlide>
              }) : null
            }
          </SplideTrack>

          <div className="splide__arrows">
            <div className="splide__arrow splide__arrow--next ts_csl_splide__arrow">
              <div className="splideArrow_inner">
                <div className="tb__icon tb-chevron-right"><div></div></div> 
              </div>
            </div>

            <div className="splide__arrow splide__arrow--prev ts_csl_splide__arrow">
              <div className="splideArrow_inner">
                <div className="tb__icon tb-chevron-left"><div></div></div>
              </div>
            </div>


          </div>


        </Splide>
      </div>
    );
  }
}


const mapStateToProps = state => {
  const { appData } = state;
  return {
    appData: appData,
    wall: appData.wall,
    loaderData: state.loaderData,
    newPostData: appData.newPostData,
    appendData: appData.postData.appendData,
    hasMoreData: appData.postData.hasMoreData[0] ? appData.postData.hasMoreData[0].hasMoreData == false ? false : appData.postData.hasMoreData[appData.postData.appendData.networkID] ? appData.postData.hasMoreData[appData.postData.appendData.networkID].hasMoreData : true : true
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow)),
    showPopUP: (data) => dispatch(showPopUP(data)),

  }
}
export default connect(mapStateToProps, mapDispatchToProps)(CircleSlider);