import React, { PureComponent } from "react";
import { RATINGS_PATH, reactStringReplace } from '../../../../constants'
import { STRING_TO_URL_CONVERT } from '../../../../utils'
import ReactHtmlParser from 'react-html-parser';
import {convertHtmlStringToRender} from '../../../Components/customFunction'
const checkStringInNotNull = (string) => string != null && string !== "" && string !== 0 ? true : false;

export default class AuthorContent extends PureComponent {
  render() {    
    const { wall, item } = this.props; 
    const ts_cs_content ={fontFamily: wall.ThemeRule.css_font, fontSize: item.type==1 ? wall.Personalization.textDecorate==1 ? wall.ThemeRule.fontSizeDecorate : wall.ThemeRule.fontSizeNormal : wall.ThemeRule.fontSize, color:wall.ThemeRule.cardColor === "#000000" && wall.ThemeRule.fontColor === "#000000" ? "#ffffff" :    wall.ThemeRule.cardColor === "#ffffff" && wall.ThemeRule.fontColor === "#ffffff" ? "#000000" : wall.ThemeRule.fontColor}
    const tb_post_rating ={backgroundImage: `url(${RATINGS_PATH}${item.networkId}/${item.rating}.png)`}
    const hashTag = { color: wall.Personalization.hashtag_color, fontWeight: "bold" }
    let content = STRING_TO_URL_CONVERT(item.content);
    const isCta = checkStringInNotNull(item.cta); const isInstaUser = item.author.isInstaUser; const postAuthor = wall.Personalization.postAuthor;
    return (<>
          {item.rating ? <div className="tb_post_rating" style={tb_post_rating} ><div></div></div>:''}
          {isCta ? <div onClick={this.onCtaClick}> {convertHtmlStringToRender(item.cta)}</div> : null}
         <div className={`ts_cs_content tb-cTSfont-${wall.ThemeRule.font_varient}${wall.Personalization.textDecorate === 1 && item.type === 1 ? item.textDecoClass : ""}`} style={ts_cs_content}> 
         
         {
          wall.Personalization.hashtag_highlight !== 0 ?
            wall.Personalization.hashtag_all === 1 ? reactStringReplace(ReactHtmlParser(content), /#(\w+)/g, (match, i) => (<div className="ts_cs_hash_tag" key={match + i} style={hashTag}>#{match}</div>))
              :wall.Personalization.hashtag_feed === 1 ? reactStringReplace(ReactHtmlParser(content), item.hash.hashString, (match, i) => (<div className="ts_cs_hash_tag" key={match + i} style={hashTag}>{match}</div>)) 
                : ReactHtmlParser(content)
                 : ReactHtmlParser(content)
         }
         </div>
      </>
    );
  }
}
