import React, { PureComponent } from "react";
import { isValidAuthorImage, TWITTER_LOGO_FN,  } from '../../../../utils';
import {convertHtmlStringToRender} from '../../../Components/customFunction'
import { themePostTracking } from '../../../../../../../../../../actions/themeActions/themeActions';
import moment from 'moment';
import AuthorAvatar from '../../Elements/AuthorAvatar'
import { TWITTER_LOGO } from "../../../../../../../../../../actions/api";
const checkStringInNotNull = (string) => string != null && string !== "" && string !== 0 ? true : false;

export default class AuthorDetails extends PureComponent {
  state = { isAuthorImageValid: true }
  componentDidMount() {
    const { item } = this.props;
    const isAuthorImagevalid = isValidAuthorImage(item.author.picture);
    this.setState({ isAuthorImagevalid: isAuthorImagevalid });
  }
  onCtaClick = event => {
    const { wall, item } = this.props;
    themePostTracking({
      type: 1,
      action: 2,
      wall: wall.Wall.id,
      feed: item.feedId,
      post: item.referenceId,
    })
  }
  render() {
    const { wall, item, network, cardWidth } = this.props;
    const { isAuthorImagevalid } = this.state; const postTime = wall.Personalization.postTime;
    const isCta = checkStringInNotNull(item.cta); const isInstaUser = item.author.isInstaUser; const postAuthor = wall.Personalization.postAuthor;
    const networkColor = wall.ThemeRule.iconType == 505 ? network.color : wall.ThemeRule.iconColor; const networkIcon = network.icon.replace('fa-', '');
    const BGImage = { backgroundImage: `url(${item.author.picture})` };
    const tb_authorBgColor = { backgroundColor: wall.ThemeRule.authorColor };
    const tb_authorColor = { color: wall.ThemeRule.authorColor };
    const socialIconCss = cardWidth <= 700 && cardWidth > 500 ? { fontSize: '26px' }
    : cardWidth <= 500 && cardWidth > 350 ? { fontSize: '22px' }
      : cardWidth <= 350 && cardWidth > 250 ? { fontSize: '20px' }
        : cardWidth <= 250 && cardWidth >= 150 ? { fontSize: '16px' }
          : cardWidth < 150 && cardWidth > 100 ? { fontSize: '16px' } : 
          cardWidth <= 100 && cardWidth > 50 ? { fontSize: '12px' } : {}
    return (<>
      
      <div className="ts_cs_author_wrapper">
        {!isInstaUser ?
          <div className="ts_cs_author">
             <div className="ts_cls_author_profile" >  <img loading="lazy"  className="ts_cls_author_profile_img" src={item.author.picture} onError={(e)=>{e.target.onerror = null;  e.target.src=item.author.errorPic}} width="64" height="64" alt="image"/></div>
            <div className="ts_cs_author_info">{postAuthor ? <div className="ts_cs_authorname" style={tb_authorColor}>{item.author.name}</div> : ''}
              <div className="ts_cs_post_info">
                {postAuthor ? <div className="ts_cs_username" style={tb_authorColor}>@{item.author.username}</div> : ''}
                {postTime ? <>
                  {postAuthor ? <div className="ts_cs_seprator" style={tb_authorBgColor}><div></div></div> : ''}
                  <div className="ts_cs_time" style={tb_authorColor}>{moment(new Date(item.createdAt * 1000)).fromNow()}</div></>
                  : ''}
              </div>
            </div>
          </div>
          : null
        }
        {item.rating != null && item.rating != 0 ? '':
        <div className="ts_cs_social_">
          <div className={`tb__icon tb-${networkIcon}`} style={{ color: networkColor }}><div></div></div>
        </div>
  }
      </div>
     
    </>
    );
  }
}
